import React from 'react';
import {BaseHeaderProps, Header, HeaderTag} from "../../../Foundations/Header";
import {FontWeight} from "../../../Foundations/Text";
import {header__primary} from "./header-primary-styles";

export function HeaderPrimary({children, style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    return <Header level={HeaderTag.Heading1} fontWeight={FontWeight.Bolder} style={{...header__primary, ...style}} {...headerProps}>
        {children}
    </Header>;
}
