import React from 'react';
import {BaseHeaderProps, Header, HeaderTag} from "../../../Foundations/Header";
import {header__bold} from "./header-bold-styles";

export function HeaderBold({children, style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    return <Header level={HeaderTag.Heading1} style={{...header__bold, ...style}} {...headerProps}>
        {children}
    </Header>;
}
