import {BaseProps} from '../types';

export enum FontVariant {
    Normal = 'normal',
    SmallCaps = 'small-caps'
}

export enum FontStyle {
    Normal = 'normal',
    Italic = 'italic',
    Oblique = 'oblique'
}

export enum FontWeight {
    Normal = 'normal',
    Bold = 'bold',
    Bolder = 'bolder',
    Lighter = 'lighter',
    W1 = 100,
    W2 = 200,
    W3 = 300,
    W4 = 400,
    W5 = 500,
    W6 = 600,
    W7 = 700,
    W8 = 800,
    W9 = 900
}

export enum TextAlign {
    Center = 'center',
    Left = 'left',
    Right = 'right',
    Justify = 'justify'
}

export interface FontProps {
    fontVariant?: FontVariant;
    fontStyle?: FontStyle;
    fontWeight?: FontWeight;
    textAlign?: TextAlign;
    fontSize?: number | string;
    lineHeight?: number | string;
    fontFamily?: string;
}

export enum TextTag {
    Paragraph = 'p',
    Span = 'span',
    Anchor = 'a',
    Heading1 = 'h1',
    Heading2 = 'h2',
    Heading3 = 'h3',
    Heading4 = 'h4',
    Heading5 = 'h5',
    Heading6 = 'h6',
    Strong = 'strong',
    Emphasized = 'em',
    Bold = 'b',
    Italic = 'i',
    BlockQuote = 'blockquote',
    InlineQuote = 'q',
    Code = 'code',
    Preformatted = 'pre',
    Mark = 'mark',
    Insert = 'ins',
    Delete = 'del',
    Subscript = 'sub',
    Superscript = 'sup',
    Small = 'small'
}

export type BaseTextProps = FontProps & BaseProps;

export interface TextProps extends BaseTextProps {
    tag: TextTag;
    text: string;
}
