import React, {CSSProperties, PropsWithChildren} from 'react';
import {Surface} from '../../Foundations/Surface';
import {HeaderPrimary} from '../../Components/Headers/HeaderPrimary';
import {FlexDirection, Flexer, FlexMainAxis, FlexSecondaryAxis} from '../../Foundations/Flexer';
import {useBreakpoint} from '../../Foundations/utilities/utilities';
import {Image} from '../../Foundations/Image';
import {Paragraph} from '../../Foundations/Paragraph';
import {Border} from '../../Foundations/Border';
import {TextAlign} from '../../Foundations/Text';
import {Shadow} from '../../Foundations/Shadow';
import {
    BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall
} from '../../Foundations/utilities/media_query_manager';
import {font__voicePrimary, font__voicePrimaryLeadin} from '../../styles/fonts';

function AboutParagraph({children, style = {}}: PropsWithChildren<{ style?: CSSProperties }>) {
    const bp = useBreakpoint();
    let marginBottom = 0;
    switch (bp) {
        case BreakpointSmall:
            marginBottom = 36;
            break;
        case BreakpointMedium:
            marginBottom = 36;
            break;
        case BreakpointLarge:
            break;
        case BreakpointExtraLarge:
            break;
        default:
            break;
    }

    return <Paragraph textAlign={TextAlign.Center}
                      style={{
                          ...font__voicePrimary,
                          ...style,
                          padding: '0 18px',
                          marginBottom,
                          display: 'inline'
                      }}>
        {children}
    </Paragraph>
}

function AboutImage() {
    const bp = useBreakpoint();
    let width: string | number = 'initial';
    let marginTop = '0px';
    switch (bp) {
        case BreakpointSmall:
            marginTop = '12px';
            width = '90%';
            break;
        case BreakpointMedium:
            marginTop = '12px';
            width = '90%'
            break;
        case BreakpointLarge:
            width = 540;
            break;
        case BreakpointExtraLarge:
            break;
        default:
            break;
    }

    return <Surface width={width} classes={['about-image']} style={{margin: '0 auto', alignSelf: 'start'}}>
        <Shadow voffset={4} spread={4} blur={8} color='rgba(0, 0, 0, .25)'>
            <Border borderRadius={8}>
                <Image src={'https://acdisaster.com/img/Alyssa-carrier-acdisaster-surveying-damage.jpg'}
                       alt={'Recovering From Disaster'}
                       width={width}
                       style={{display: 'block', margin: `${marginTop} auto`}}/>
            </Border>
        </Shadow>
    </Surface>
}

function AboutRow({children, style}: PropsWithChildren<{ style?: CSSProperties }>) {
    let direction: FlexDirection = FlexDirection.Row;
    const bp = useBreakpoint();
    switch (bp) {
        case BreakpointSmall:
            direction = FlexDirection.Column;
            break;
        case BreakpointMedium:
            direction = FlexDirection.Column;
            break;
        case BreakpointLarge:
            direction = FlexDirection.Row;
            break;
        case BreakpointExtraLarge:
            direction = FlexDirection.Row;
            break;
        default:
            break;
    }
    return <Flexer direction={direction} main={FlexMainAxis.Center}>
        <Surface style={{...style, padding: 12}}>
            {children}
        </Surface>
    </Flexer>;
}

