import {CSSProperties, SyntheticEvent, useEffect, useRef, useState} from "react";
import {EventHandler} from "../types";
import styleManager from "./style_manager";
import idManager from './id_manager';
import {mediaManager} from "./media_query_manager";

const RegReplacer = require('regreplacer').default;

export function pxToRem(px: number) {
    const root = 10;
    return `${px / root}rem`;
}

export function pxToEm(px: number) {
    const root = 16;
    return `${px / root}em`;
}

export function ensureNotPx(unit: number | string) {
    if (typeof unit === 'string') {
        if (unit.includes('px')) {
            const val = parseFloat(unit.replace('px', ''));
            unit = pxToRem(val);
        }
    } else {
        unit = pxToRem(unit);
    }
    return unit;
}

export function ensureNotPxEm(unit: number | string) {
    if (typeof unit === 'string') {
        if (unit.includes('px')) {
            const val = parseFloat(unit.replace('px', ''));
            unit = pxToRem(val);
        }
    } else {
        unit = pxToEm(unit);
    }
    return unit;
}

export function targetOnly(fn: EventHandler): EventHandler {
    return (e: SyntheticEvent) => {
        if (e.target === e.currentTarget) {
            e.stopPropagation();
            fn(e);
            return;
        }
        e.stopPropagation();
    }
}

export function useAnonymousStyle(styles: CSSProperties, subSelector: string = '') {
    const ref = useRef<string>(idManager.genId(2));
    if (ref.current) idManager.addId(ref.current);
    const selector = `.${ref.current}${subSelector}`;
    useStyle(selector, styles);
    useEffect(() => {
        const {current} = ref;
        return () => {
            idManager.removeId(current);
        }
    }, [styles, subSelector]);
    return ref;
}

const SHOULD_CONVERT: { [key: string]: boolean } = {
    borderRadius: true,
    borderWidth: true,
    maxWidth: true,
    width: true,
    height: true,
    transformOrigin: true,
    margin: true,
    marginLeft: true,
    marginRight: true,
    marginBottom: true,
    marginTop: true,
    padding: true,
    paddingLeft: true,
    paddingRight: true,
    paddingBottom: true,
    paddingTop: true,
    fontSize: true,
    lineHeight: true,
    borderBottomRightRadius: true,
    borderBottomLeftRadius: true,
    minWidth: true
};

const GRAB_PX_REGEX = /(\d+)px/g;
const regRep = new RegReplacer(GRAB_PX_REGEX);

function convertStyleToRem(styleName: string, styleValue: string | number) {
    let out = styleValue;
    if (SHOULD_CONVERT[styleName]) {
        if (typeof styleValue === 'string') {
            const matches = regRep.match(styleValue);
            if (matches.hasMatches) {
                out = matches.replace(matches.matches.map(ensureNotPx), 'matches');
            }
        } else {
            out = ensureNotPx(styleValue);
        }
    }
    // console.log(styleName, out);
    return out;
}

function convertStylesToRem(styles: CSSProperties) {
    const out: { [key: string]: string | number } = {};
    for (const [key, value] of Object.entries(styles)) {
        out[key] = convertStyleToRem(key, value);
    }
    return out;
}

export function useStyle(selector: string, styles: CSSProperties) {
    const convertedStyles = convertStylesToRem(styles);
    useEffect(() => {
        styleManager.insertRule(selector, convertedStyles);
        return () => {
            styleManager.removeRule(selector);
        }
    }, [convertedStyles, selector]);
}

export function useStyles(styles: { [key: string]: CSSProperties }, base: string = '') {
    useEffect(() => {
        for (const style of Object.keys(styles)) {
            const selector = (base ? `${base}` : '') + style;
            const convertedStyles = styles[style];
            styleManager.insertRule(selector, convertedStyles);
        }
        return () => {
            for (const style of Object.keys(styles)) {
                const selector = (base ? `${base}` : '') + style;
                styleManager.removeRule(selector);
            }
        }
    }, [styles, base]);
}

export function useBreakpoint() {
    const [breakPoint, setBreakPoint] = useState<Symbol>(mediaManager.active);
    useEffect(() => {
        const handler = () => setBreakPoint(mediaManager.active);
        mediaManager.addEventListener('change', handler);
        return () => mediaManager.removeEventListener('change', handler);
    });
    return breakPoint;
}

export function shallowObjectCompare(prevProps: any, nextProps: any) {
    const keys1 = Object.keys(prevProps).sort();
    const keys2 = Object.keys(nextProps).sort();
    if (keys1.length !== keys2.length) return false;
    for (const key of keys1) {
        const prev = prevProps[key];
        const next = nextProps[key];
        if (!(key in nextProps)) {
            return false;
        } else {
            const type = typeof prev;
            if (typeof type === 'object') {
                if (typeof next !== 'object') return false;
                if (Array.isArray(prev) && !Array.isArray(next)) {
                    return false;
                }
            } else {
                if (type !== typeof next) return false;
            }
        }
    }

    for (const key of keys1) {
        const prev = prevProps[key];
        const next = nextProps[key];
        const type = typeof prev;
        switch (type) {
            case 'number':
                if (prev !== next) return false;
                break;
            case 'string':
                if (prev !== next) return false;
                break;
            case 'object': {
                if (Array.isArray(prev)) {
                    for (let i = 0; i < prev.length; i++) {
                        if (prev[i] !== next[i]) return false;
                    }
                } else {
                    const subKeys1 = Object.keys(prev);
                    const subKeys2 = Object.keys(next);
                    if (subKeys1.length !== subKeys2.length) return false;
                    for (const key of subKeys1) {
                        if (!(key in next)) {
                            return false;
                        } else {
                            if (prevProps[key] !== nextProps[key]) return false;
                        }
                    }
                }
            }
        }
    }
    return true;
}
