import React, {PropsWithChildren} from 'react';
import {PresentationProps} from "../../Foundations/types";
import {Surface} from "../../Foundations/Surface";
import {Border} from "../../Foundations/Border";
import {Paragraph} from "../../Foundations/Paragraph";
import {FontStyle, TextAlign} from "../../Foundations/Text/text-types";
import {useBreakpoint} from "../../Foundations/utilities/utilities";
import {
    BreakpointExtraLarge,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall
} from "../../Foundations/utilities/media_query_manager";
import {font__voicePrimary, font__voicePrimaryLeadin} from "../../styles/fonts";

export function TestimonialText({children}: PropsWithChildren<{}>) {
    const text = `"${children}"`;
    return <Paragraph fontStyle={FontStyle.Italic} textAlign={TextAlign.Center}
                      style={{...font__voicePrimary, padding: '36px 36px 6px 36px'}}>
        {text}
    </Paragraph>
}

export interface TestimonialNameProps {
    name: string;
    title: string;
}

export function TestimonialName({name, title}: TestimonialNameProps) {
    return <Surface style={{padding: '6px 36px 12px 36px'}}>
        <Paragraph style={font__voicePrimaryLeadin}>{name}</Paragraph>
        <Paragraph style={font__voicePrimary}>
            {title}
        </Paragraph>
    </Surface>

}

export interface TestimonialProps extends PresentationProps {

}

export function Testimonial({children, style = {}, classes = [], ...props}: TestimonialProps) {
    const {background} = props;
    let width = '65%';
    const bp = useBreakpoint();
    switch (bp) {
        case BreakpointSmall:
            width = '90%';
            break;
        case BreakpointMedium:
            width = '90%';
            break;
        case BreakpointLarge:
            width = '65%';
            break;
        case BreakpointExtraLarge:
            width = '65%';
            break;
        default:
            break;
    }
    return <Surface background={background} width='100%' style={{...style, overflow: 'auto'}}>
        <Border borderRadius={8}>
            <Surface background='white' width={width} style={{margin: '48px auto 48px auto'}}>
                {children}
            </Surface>
        </Border>
    </Surface>;
}
