import {useAnonymousStyle, useStyles} from '../utilities/utilities';
import {PropsCloner} from '../PropsCloner';
import {HoverProps} from './hover-types';

export function Hover({children, style = {}, classes = [], hoverStyles = {}, subStyles = {}}: HoverProps) {
    const ref = useAnonymousStyle(hoverStyles, ':hover');
    useStyles(subStyles, `.${ref.current}:hover`);
    return PropsCloner({children, props: {style, classes: [...classes, ref.current]}});
}
