import React from 'react';
import {Text, TextTag} from '../Text';
import {shallowObjectCompare} from '../utilities/utilities';
import {HeaderProps} from './header-types';

function _Header({children, style = {}, classes = [], level, ...props}: HeaderProps) {
    if (typeof children !== 'string') throw Error('Can only pass a string child to Header');
    return <Text tag={(level as unknown) as TextTag} text={children} style={style} classes={classes} {...props} />
}

export const Header = React.memo(_Header, shallowObjectCompare);
