import React, {createElement} from 'react';
import {TextProps, TextTag} from './text-types';
import {shallowObjectCompare, useAnonymousStyle} from '../utilities/utilities';
import makeClassString from 'classnames';

interface RawTextProps {
    tag: TextTag,
    className?: string;
    text: string;
}

function _RawText({tag, className, text}: RawTextProps) {
    return createElement(tag, {className}, text);
}

const RawText = React.memo(_RawText);

function _Text({style = {}, classes = [], tag, text, ...props}: TextProps) {
    const ref = useAnonymousStyle({...style, ...props});
    const finalClassName = makeClassString(classes, ref.current);
    return <RawText tag={tag} text={text} className={finalClassName}/>
}

export const Text = React.memo(_Text, shallowObjectCompare);
