import React, {useEffect} from 'react';
import {PositionBlock, PositionTypes} from "../../Foundations/PositionBlock";
import {ComponentProps} from "../../Foundations/types";
import {Flexer, FlexMainAxis, FlexSecondaryAxis} from "../../Foundations/Flexer";
import {targetOnly} from "../../Foundations/utilities/utilities";
import {Surface} from "../../Foundations/Surface";
import {ActionProps} from "../../Foundations/Action/action-types";
import {Action} from "../../Foundations/Action/Action";

type ModalBaseProps = ComponentProps & ActionProps;

export interface ModalProps extends ModalBaseProps {
    background?: string;
    open: boolean;
}

export function Modal({children, style = {}, classes = [], open, ...props}: ModalProps) {
    const {background, onClickCapture = () => ({})} = props;
    useEffect(() => {
        const body = document.getElementsByTagName('body')[0];
        if (open) body.style.overflow = 'hidden';
        return () => {body.style.overflow = 'initial'};
    });
    const visibility = open ? 'initial' : 'hidden';
    return <PositionBlock style={{width: '100%', zIndex: 9999, visibility}} top={0} left={0} position={PositionTypes.Fixed}>
            <Flexer style={{...style}} classes={classes} main={FlexMainAxis.Center}
                    secondary={FlexSecondaryAxis.Center}>
                <Action onClickCapture={targetOnly(onClickCapture)}>
                    <Surface width='100%' height='100vh' background={background}>
                        {children}
                    </Surface>
                </Action>
            </Flexer>
        </PositionBlock>;
}
