import React from 'react';
import makeClassString from 'classnames';
import {useAnonymousStyle} from '../utilities/utilities';
import {SurfaceProps} from './surface-types';

export function Surface({children, style = {}, classes = [], width, height, background, ...props}: SurfaceProps) {
    const ref = useAnonymousStyle({...props, ...style, width, height, background});
    const className = makeClassString(classes, ref.current);
    return <div {...props} className={className}>
        {children}
    </div>;
}
